      }
    }

    .select-container {
      justify-content: flex-end;
      min-height: 2.5em;
    }

    @media (max-width: $br-sm) {
      padding: 0.3em 0.6em 0.3em 0.6em;
      align-items: center;
      h1 {
        font-size: 1em;
        flex-grow: 1;
      }

      select-container {
        flex-grow: 0;
        min-height: auto;
      }
    }

    h3 {
      margin: 0;
      padding: 0;
    }
  }

  main {
    display: flex;
