    min-height: 100vh;
    background-color: $c-white;
    max-width: 100vw;
  }

  section {
    // border-top: 1px solid $c-gray-4;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;
    max-width: 100vw;
    width: 100%;

    @include fluid-space(padding, $sp-medium);
    @media (max-width: $br-sm) {
      @include fluid-space(padding, $sp-base);
    }
    h2 {
      max-width: 40rem;
      line-height: 1.3;
    }
